import classnames from 'classnames';
import styles from './steps.module.scss';
import { steps } from '../infos/infos';
// import house from './../../assets/animations/house.json';
import house from './../../assets/animations/house.lottie';
// import size from './../../assets/animations/size.json';
import size from './../../assets/animations/size.lottie';
// import bulb from './../../assets/animations/bulb.json';
import bulb from './../../assets/animations/bulb.lottie';
import arrow from './../../assets/animations/arrow-down.lottie';
import 'aos/dist/aos.css';
import { DotLottiePlayer } from '@dotlottie/react-player';
import { Link } from 'react-router-dom';
import Icon from '../../elements/icon/Icon';
import { HashLink } from 'react-router-hash-link';

const Steps = () => {
  return (
    <div id='steps' className={styles.stepsWrapper}>
      <div className={classnames(styles.container, styles.steps)}>
        <h2 data-aos='fade-right' className={styles.title}>
          Вивіска - це просто!
        </h2>
        <p data-aos='fade-right' className={styles.subtitle}>
          Що потрібно для замовлення?
        </p>
        <div className={styles.content}>
          <div
            data-aos='fade-down'
            data-aos-duration='1000'
            className={styles.cardContainer}
          >
            <div className={styles.titleContainer}>
              <div className={styles.animationContainer}>
                <DotLottiePlayer src={house} autoplay loop />
              </div>
              <h4 className={styles.cardTitle}>{steps[0].title}</h4>
            </div>

            <p
              data-aos='fade-down'
              data-aos-duration='1000'
              className={styles.text}
            >
              {steps[0].text}
            </p>
          </div>

          <div
            data-aos='fade-down'
            data-aos-duration='1000'
            className={styles.cardContainer}
          >
            <div className={styles.titleContainer}>
              <div className={styles.animationContainer}>
                <DotLottiePlayer src={size} autoplay loop />
              </div>
              <h4 className={styles.cardTitle}>{steps[1].title}</h4>
            </div>

            <p
              data-aos='fade-down'
              data-aos-duration='1000'
              className={styles.text}
            >
              {steps[1].text}
            </p>
          </div>
          <div
            data-aos='fade-down'
            data-aos-duration='1000'
            className={styles.cardContainer}
          >
            <div className={styles.titleContainer}>
              <div className={styles.animationContainerIdea}>
                {/* <Lottie animationData={bulb} /> */}
                <DotLottiePlayer src={bulb} autoplay loop />
              </div>
              <h4 className={styles.cardTitle}>{steps[2].title}</h4>
            </div>

            <p
              data-aos='fade-down'
              data-aos-duration='1000'
              className={styles.text}
            >
              {steps[2].text}
            </p>
          </div>
        </div>
        <HashLink smooth to='#titleSecond' className={styles.arrowDown}>
          <DotLottiePlayer src={arrow} autoplay loop />
        </HashLink>
        <h3
          id='titleSecond'
          data-aos='fade-right'
          className={styles.titleSecond}
        >
          Зв'яжіться з нами
        </h3>

        <div className={styles.buttons}>
          <div className={styles.buttonContainer}>
            <h4 data-aos='fade-right' className={styles.subtitleSecond}>
              Через месенджери:
            </h4>
            <a
              data-aos='fade-right'
              className={classnames(styles.link, styles.linkSecond)}
              href='https://wa.me/380993377739'
            >
              <span>WhatsApp</span>
              <div className={styles.iconContainer}>
                <Icon class={styles.iconTelegram} iconName='whatsapp' />
              </div>
            </a>
            <a
              data-aos='fade-right'
              className={classnames(styles.link, styles.linkSecond)}
              href='href="https://t.me/agvento1"'
            >
              <span>Telegram</span>
              <div className={styles.iconContainer}>
                <Icon class={styles.iconTelegram} iconName='telegram-solid' />
              </div>
            </a>
            <a
              data-aos='fade-right'
              className={classnames(styles.link, styles.linkSecond)}
              href='viber://chat/?number=%2B380993377739'
            >
              <span>Viber</span>
              <div className={styles.iconContainer}>
                <Icon class={styles.iconTelegram} iconName='viber' />
              </div>
            </a>
          </div>
          <div className={styles.buttonContainer}>
            <h4 data-aos='fade-right' className={styles.subtitleSecond}>
              По телефону:
            </h4>
            <a
              data-aos='fade-right'
              className={classnames(styles.link, styles.linkSecond)}
              href='tel:+380993377739'
            >
              <span>Водафон</span>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Steps;
