import styles from './service.module.scss';

// Swiper
import './swiper.scss';

// ClassNames
import { Navigation, Pagination, A11y, Autoplay } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/scss/navigation';
import 'swiper/scss/pagination';
import OldAds from './oldAds/OldAds';

const Service = (props) => {
  return (
    <>
      <div
        id={props.id}
        className={
          props.description === ''
            ? styles.noDesc
            : props.id % 2 === 0
            ? styles.serviceReverse
            : styles.service
        }
      >
        <div
          className={
            props.description === ''
              ? styles.noDescInfos
              : styles.infosContainer
          }
        >
          <div
            data-aos='fade-down'
            data-aos-duration='1000'
            className={styles.numberContainer}
          >
            <div className={styles.circle}>
              <span className={styles.number}>{props.id}</span>
            </div>
            <h2 className={styles.title}>{props.service}</h2>
          </div>
          <p
            data-aos='fade-down'
            data-aos-duration='1000'
            className={styles.description}
          >
            {props.description}
          </p>
          {props.id === 11 ? <OldAds /> : ''}
        </div>
        <div
          data-aos={
            props.description === ''
              ? 'fade-down'
              : props.id % 2 === 0
              ? 'fade-down'
              : 'fade-down'
          }
          data-aos-duration='1000'
          className={
            props.description === '' ? styles.noDescImages : styles.images
          }
        >
          <Swiper
            install
            Swiper
            modules
            modules={[Navigation, Pagination, A11y, Autoplay]}
            spaceBetween={50}
            slidesPerView={props.description === '' ? 1 : 1}
            breakpoints={
              props.description === ''
                ? {
                    1300: {
                      slidesPerView: 1,
                    },
                  }
                : 1
            }
            speed={
              props.description === '' ? 600 : props.id % 2 === 0 ? 500 : 400
            }
            loop={true}
            navigation
            // pagination={{ clickable: true }}
            autoplay={true}
            className={props.description === '' ? 'noDesc' : 'service'}
          >
            {props.photos &&
              props.photos.map((photo, index) => (
                <SwiperSlide className={styles.swiperSlide}>
                  <img
                    className={styles.image}
                    src={photo}
                    onClick={() => {
                      props.setActiveSliderId(props.id);
                      props.setOpenImg(!props.openImg);
                      props.setActiveIndex(index);
                    }}
                  />
                </SwiperSlide>
              ))}
          </Swiper>
        </div>
      </div>
      <div className={styles.line}></div>
    </>
  );
};

export default Service;
