import classnames from 'classnames';
import Header from './../components/header/Header';
import styles from './services-page.module.scss';
import { services } from '../components/infos/infos';
import { HashLink, HashLink as Link } from 'react-router-hash-link';
import Icon from '../elements/icon/Icon';
import { useEffect, useRef, useState } from 'react';
import Service from '../components/service-page/Service';
import Footer from '../components/footer/Footer';
// import arrowDown from './../assets/animations/arrow-down.json';
import arrowDown from './../assets/animations/arrow-down.lottie';
import Lottie from 'lottie-react';
import bg from './../assets/images/bg-3.png';
import ModalUp from './../components/modal-up/ModalUp';
import './openSlider.scss';
// import cross from './../assets/animations/cross.json';
import cross from './../assets/animations/cross.lottie';

// Swiper
// import './swiper.scss';
import { Navigation, Pagination, A11y } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/scss/navigation';
import 'swiper/scss/pagination';
import {
  TransformComponent,
  TransformWrapper,
  useControls,
} from 'react-zoom-pan-pinch';
import { DotLottiePlayer } from '@dotlottie/react-player';
import Catalog from '../components/catalog/Catalog';

const ServicesPage = () => {
  const [closeSidebar, setCloseSidebar] = useState(false);
  const [openImg, setOpenImg] = useState(false);
  const [activeSliderId, setActiveSliderId] = useState('');
  const [activeIndex, setActiveIndex] = useState('');
  const [line, setLine] = useState(false);

const Controls = () => {
  const { zoomIn, zoomOut, resetTransform } = useControls();
  return (
    <div className={styles.controlsContainer}>
      <button
        className={styles.controlButton}
        onClick={(e) => {
          zoomIn();
          e.stopPropagation();
        }}
      >
        <span className={styles.big}>Збільшити</span>
        <Icon class={styles.small} iconName='expand' />
      </button>
      <button
        className={styles.controlButton}
        onClick={(e) => {
          zoomOut();
          e.stopPropagation();
        }}
      >
        <span className={styles.big}>Зменшити</span>
        <Icon class={styles.small} iconName='collapse' />
      </button>
      <button
        className={styles.controlButton}
        onClick={(e) => {
          resetTransform();
          e.stopPropagation();
        }}
      >
        <span className={styles.big}>Скинути</span>
        <Icon class={styles.small} iconName='restart' />
      </button>
    </div>
  );
};


  return (
    <div id='services' className={styles.servicePageWrapper}>
      {openImg && (
        <div
          className={styles.openImagesWrapper}
          onClick={() => setOpenImg(false)}
        >
          {services.map((service) => (
            <div className={styles.openImagesContainer}>
              {activeSliderId === service.id &&
                service.photos &&
                service.photos
                  .filter((item, index) => index === activeIndex)
                  .map((filteredImage) => (
                    <TransformWrapper>
                      <TransformComponent>
                        <div
                          onClick={(e) => e.stopPropagation()}
                          className={styles.openImageContainer}
                        >
                          <img src={filteredImage} />
                        </div>{' '}
                      </TransformComponent>
                      <Controls />
                    </TransformWrapper>
                  ))}
            </div>
          ))}
          <div className={styles.crossContainer}>
            <Icon iconName='cross' />
          </div>
        </div>
      )}
      <ModalUp />
      <div
        className={
          closeSidebar ? styles.servicesWrapperOff : styles.servicesWrapper
        }
      >
        <Header />

        <div
          className={classnames(
            styles.containerServices,
            styles.contentContainer
          )}
        >
          <div
            className={classnames(styles.list, {
              [styles.listClose]: closeSidebar,
            })}
          >
            <div className={styles.content}>
              <div className={styles.links}>
                {services.map((service) => (
                  <div className={styles.service} key={service.service}>
                    <Link smooth className={styles.link} to={`#${service.id}`}>
                      <span className={styles.text}>{service.service}</span>
                      <span className={styles.iconContainer}>
                        <Icon class={styles.iconArrow} iconName='arrow-right' />
                      </span>
                    </Link>
                    <div className={styles.line}></div>
                  </div>
                ))}
              </div>
            </div>
            <div
              onClick={() => setCloseSidebar(!closeSidebar)}
              className={styles.buttonContainer}
            >
              <div className={styles.button}>
                {closeSidebar ? (
                  <Icon iconName='wide-arrow' class={styles.icon} />
                ) : (
                  <Icon iconName='arrow-wide-left' class={styles.icon} />
                )}
              </div>
            </div>
          </div>
          <div className={classnames(styles.container, styles.services)}>
            <h2 id='title' data-aos='fade-right' className={styles.title}>
              Наші послуги
            </h2>
            <h3 data-aos='fade-right' className={styles.subtitle}>
              Тут Ви знайдете наші роботи:
            </h3>
            <HashLink smooth to={`#${services[0].id}`} className={styles.arrow}>
              {/* <Lottie animationData={arrowDown} /> */}
              <DotLottiePlayer src={arrowDown} autoplay loop />
            </HashLink>
            <Catalog line={line} setLine={setLine} />
            <div
              className={
                line ? styles.catalogueLineActive : styles.catalogueLine
              }
            ></div>
            {services.map((service) => (
              <Service
                setOpenImg={setOpenImg}
                openImg={openImg}
                service={service.service}
                description={service.description}
                photos={service.photos}
                id={service.id}
                setActiveSliderId={setActiveSliderId}
                setActiveIndex={setActiveIndex}
              />
            ))}
          </div>
        </div>
        <div className={styles.imgContainer}>
          <img src={bg} alt='block background image' />
        </div>
      </div>
      <div className={styles.footerWrapper}>
        <Footer />
      </div>
    </div>
  );
};

export default ServicesPage;
