import classnames from 'classnames';
import styles from './reviews.module.scss';
import bg from './../../assets/images/bg-3.png';
import { reviews } from './../infos/infos';

// Swiper
import './swiper.scss';
import Icon from '../../elements/icon/Icon';
import 'aos/dist/aos.css';

// ClassNames
import { Navigation, Pagination, A11y, Autoplay } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/scss/navigation';
import 'swiper/scss/pagination';

const Reviews = () => {
  return (
    <div id='reviews' className={styles.reviewsWrapper}>
      <div className={styles.reviewsContainer}>
        <div className={styles.imgContainer}>
          <img src={bg} alt='block background image' />
        </div>
        <div className={classnames(styles.container, styles.reviews)}>
          <h2 data-aos='fade-right' className={styles.title}>
            Відгуки
          </h2>
          <h3 data-aos='fade-right' className={styles.subtitle}>
            Наші клієнти пишуть про нас:
          </h3>
          {/* <div className={styles.line}></div> */}
          <div data-aos='zoom-in-up' className={styles.slider}>
            <Swiper
              install
              Swiper
              modules
              modules={[Navigation, Pagination, A11y, Autoplay]}
              spaceBetween={50}
              slidesPerView={2}
              breakpoints={{
                1400: {
                  slidesPerView: 2,
                },
                320: {
                  slidesPerView: 1,
                }
              }}
              speed={500}
              loop={true}
              navigation
              // pagination={{ clickable: true }}
              // autoplay={true}
              className='reviews'
            >
              {reviews.map((review, index) => (
                <SwiperSlide className={styles.swiperSlide}>
                  <div key={`li-${index}`} className={styles.slide}>
                    <div className={styles.stars}>
                      <div className={styles.iconContainer}>
                        <Icon iconName='star' />
                      </div>
                      <div className={styles.iconContainer}>
                        <Icon iconName='star' />
                      </div>
                      <div className={styles.iconContainer}>
                        <Icon iconName='star' />
                      </div>
                      <div className={styles.iconContainer}>
                        <Icon iconName='star' />
                      </div>
                      <div className={styles.iconContainer}>
                        <Icon iconName='star' />
                      </div>
                    </div>
                    <div className={styles.text}>{review.text}</div>
                    <div className={styles.name}>{review.name}</div>
                  </div>
                </SwiperSlide>
              ))}
            </Swiper>
          </div>
        </div>
        <div className={styles.imgContainerSecond}>
          <img src={bg} alt='block background image' />
        </div>
      </div>
    </div>
  );
};

export default Reviews;
