import classnames from 'classnames';
import styles from './passport.module.scss';
import { Link } from 'react-router-dom';
// import arrowWhite from './../../assets/animations/arrow-white.json';
import arrowWhite from './../../assets/animations/arrow-white.lottie';
import bg from './../../assets/images/bg-3.png';
import { DotLottiePlayer } from '@dotlottie/react-player';
import { HashLink } from 'react-router-hash-link';

const Passport = () => {
  return (
    <div id='passport' className={styles.passportWrapper}>
      <div className={styles.imgContainer}>
        <img src={bg} alt='block background image' />
      </div>
      <div className={classnames(styles.container, styles.passport)}>
        <h2 data-aos='zoom-in-up' className={styles.title}>
          Паспорт вивіски (погодження вивіски)
        </h2>
        <HashLink
          smooth
          to='/passport#top'
          data-aos='zoom-in-up'
          className={styles.link}
        >
          <h3 className={styles.subtitle}>Перейти</h3>
          <div className={styles.animation}>
            {/* <Lottie animationData={arrowWhite} /> */}
            <DotLottiePlayer src={arrowWhite} autoplay loop />
          </div>
        </HashLink>
      </div>
      <div className={styles.imgContainerSecond}>
        <img src={bg} alt='block background image' />
      </div>
    </div>
  );
};

export default Passport;
