import classnames from 'classnames';
import styles from './banner.module.scss';
import { Link } from 'react-router-dom';
import Icon from '../../elements/icon/Icon';
import './cursor.scss';
import 'aos/dist/aos.css';
import arrow from './../../assets/animations/arrow-down.lottie';
import { DotLottiePlayer } from '@dotlottie/react-player';
import '@dotlottie/react-player/dist/index.css';
import { HashLink } from 'react-router-hash-link';
import bg from './../../assets/images/bg-3.png';

const Banner = () => {
  return (
    <div id='banner' className={styles.bannerWrapper}>
      <div className={classnames(styles.banner)}>
        <div className={styles.contentContainer}>
          <div data-aos='fade-zoom-in' className={styles.content}>
            <div className={styles.titleContainer}>
              <h1 className={styles.title}>Власне виробництво</h1>
            </div>
            <div>
              <h2 className={styles.subtitle}>всіх видів реклами</h2>
            </div>
            <div className='cursor'></div>
            <div className={styles.buttons}>
              <div className={styles.linkContainer}>
                <Link
                  data-aos='fade-right'
                  className={classnames(styles.linkFirst)}
                  to='/services'
                >
                  <span>Тут можна подивитись </span>{' '}
                  <div className={styles.textLink}>
                    <span className={styles.underline}> наші роботи</span>
                    <div className={styles.arrow}>
                      <DotLottiePlayer src={arrow} autoplay loop />
                    </div>
                  </div>
                </Link>
              </div>
              <div className={styles.links}>
                <div>
                  <a
                    data-aos='fade-right'
                    className={classnames(styles.link, styles.linkSecond)}
                    to='#'
                  >
                    <span>Наш телеграм</span>
                    <div className={styles.iconContainer}>
                      <Icon class={styles.icon} iconName='telegram-solid' />
                    </div>
                  </a>
                </div>
                <div>
                  <a
                    data-aos='fade-right'
                    className={classnames(styles.link, styles.linkSecond)}
                    href='viber://chat/?number=%2B380993377739'
                  >
                    <span>Viber</span>
                    <div className={styles.iconContainer}>
                      <Icon class={styles.iconTelegram} iconName='viber' />
                    </div>
                  </a>
                </div>
              </div>
            </div>
          </div>
          {/* <div className={styles.imageBgContainer}>
            <img src={bg2} alt="bg2" />
          </div> */}
        </div>

        <div className={styles.arrowDown}>
          <HashLink smooth to='#mainServices'>
            <DotLottiePlayer src={arrow} autoplay loop />
          </HashLink>
        </div>
      </div>
      <div className={styles.imgContainerSecond}>
        <img src={bg} alt='block background image' />
      </div>
    </div>
  );
};

export default Banner;
