import Footer from '../components/footer/Footer';
import Header from '../components/header/Header';
import styles from './portfolioPage.module.scss';
import { portfolioImages } from '../components/infos/infos';
import classNames from 'classnames';
import { HashLink } from 'react-router-hash-link';
import Lottie from 'lottie-react';
// import cross from './../assets/animations/cross.json';
import cross from './../assets/animations/cross.lottie';
// import arrowDown from './../assets/animations/arrow-down.json';
import arrowDown from './../assets/animations/arrow-down.lottie';
import {
  TransformWrapper,
  TransformComponent,
  useControls,
} from 'react-zoom-pan-pinch';
import { useEffect, useRef, useState } from 'react';
import { DotLottiePlayer } from '@dotlottie/react-player';
import bg from './../assets/images/bg-3.png';
import ModalUp from '../components/modal-up/ModalUp';
import Icon from '../elements/icon/Icon';

const PortfolioPage = () => {
  const [openImg, setOpenImg] = useState(false);
  const [imgIndex, setImgIndex] = useState('');

  const Controls = () => {
    const { zoomIn, zoomOut, resetTransform } = useControls();
    return (
      <div className={styles.controlsContainer}>
        <button
          className={styles.controlButton}
          onClick={(e) => {
            zoomIn();
            e.stopPropagation();
          }}
        >
          <span className={styles.big}>Збільшити</span>
          <Icon class={styles.small} iconName='expand' />
        </button>
        <button
          className={styles.controlButton}
          onClick={(e) => {
            zoomOut();
            e.stopPropagation();
          }}
        >
          <span className={styles.big}>Зменшити</span>
          <Icon class={styles.small} iconName='collapse' />
        </button>
        <button
          className={styles.controlButton}
          onClick={(e) => {
            resetTransform();
            e.stopPropagation();
          }}
        >
          <span className={styles.big}>Скинути</span>
          <Icon class={styles.small} iconName='restart' />
        </button>
      </div>
    );
  };

  return (
    <div id='portfolio' className={styles.portfolioPageWrapper}>
      <ModalUp />
      <Header />
      <div className={styles.portfolioWrapper}>
        <div className={classNames(styles.container, styles.services)}>
          <h2 id='title' data-aos='fade-right' className={styles.title}>
            Наше портфоліо
          </h2>
          <h3 data-aos='fade-right' className={styles.subtitle}>
            Тут Ви знайдете приклади наших робіт:
          </h3>
          <HashLink smooth to='#imagesContainer' className={styles.arrow}>
            {/* <Lottie animationData={arrowDown} /> */}
            <DotLottiePlayer src={arrowDown} autoplay loop />
          </HashLink>
        </div>
        {!openImg && (
          <div id='imagesContainer' className={styles.imagesContainer}>
            {portfolioImages.map((item, index) => (
              <>
                <div
                  onClick={() => {
                    setOpenImg(true);
                    setImgIndex(index);
                  }}
                  className={styles.imgContainer}
                >
                  <img src={item} />
                </div>
                <div className={styles.line}></div>
              </>
            ))}
          </div>
        )}
        {openImg && (
          <div
            className={styles.openImagesContainer}
            onClick={() => setOpenImg(false)}
          >
            {portfolioImages
              .filter((item, index) => index === imgIndex)
              .map((filteredImage) => (
                <TransformWrapper>
                  <TransformComponent>
                    <div
                      onClick={(e) => e.stopPropagation()}
                      className={styles.openImageContainer}
                    >
                      <img src={filteredImage} />
                    </div>
                  </TransformComponent>
                  <Controls />
                </TransformWrapper>
              ))}
            <div className={styles.crossContainer}>
              <Icon iconName='cross'/>
            </div>
          </div>
        )}
      </div>
      <div className={styles.imgContainerTwo}>
        <img src={bg} alt='block background image' />
      </div>
      <div className={styles.footerWrapper}>
        <Footer />
      </div>
    </div>
  );
};

export default PortfolioPage;
